@font-face {
  font-family: "Cuprum";
  src: url("../fonts/cuprum-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Cuprum";
  src: url("../fonts/cuprum-italic.woff2") format("woff2");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Cuprum";
  src: url("../fonts/cuprum-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/pt-sans-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/pt-sans-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --clear-background: #FFF;
  --light-background: #F1F5F7;
  --dark-background: #32425C;
  --dark-background-extra: #293449;
  --dark-background-push: #161D29;
  --dark-background-extra-push: #1D2739;
  --header-light-background: #F2F6F8;
  --heading-background: #F9F5F0;
  --catalog-background: #F7F3EC;
  --button-container-background: #F4F4F4;
  --button-border: #E5E5E5;
  --light-border: #EAEAEA;
  --basic-type: #000;
  --basic-type-contrast: #FFF;
  --footer-type: #FFD180;
  --light-type: #C5C5C5;
  --emphasis: #EE3643;
  --emphasis-hover: #CA2C37;
  --emphasis-active: #BA2732;
  --deal: #63A63E;
  --deal-shadow: #367315;
  --deal-hover: #5FBB2D;
  --transparent-type: hsla(0, 0%, 100%, 0.5);
  --transparent-extra-type: hsla(0, 0%, 0%, 0.4);
  --basic-icon: #C5C5C5;
  --transparent-icon: hsla(0, 0%, 100%, 0.3);
  --transparent-button: hsla(0, 0%, 100%, 0.3);
  --transparent-button-hover: hsla(0, 0%, 77%, 0.3);
  --transparent-button-active: hsla(0, 0%, 66%, 0.3);
  --transparent-shadow: hsla(0, 0%, 0%, 0.15);
  --special-yellow: #FFBF47;
  --special-blue: #3BBCE3;
  --special-lillac: #DC91D8;
  --special-green: #8ED78F;
  --special-yellowlight: #FFC047;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  min-width: 960px;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: center;
  margin: 0;
  padding: 0;
  font-family: "Cuprum", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: normal;
  color: var(--basic-type);
  background-color: var(--clear-background);
}

.header {
  background-color: var(--header-light-background);
  margin-bottom: 59px;
  padding-bottom: 65px;
}

.header__top {
  font-size: 18px;
  line-height: 18px;
  color: var(--basic-type-contrast);
  background-color: var(--dark-background-extra);
  margin-bottom: 43px;
}

.header__top-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 0;
  flex-wrap: wrap;
}

.header__top-button-bookmark::before {
  width: 14px;
  height: 16px;
  background-image: url("../images/icons/bookmark.svg");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
}

.header__top-button-cart--fill {
  background-color: var(--emphasis);
}

.header__top-button-cart--fill:active {
  background-color: var(--emphasis-active);
}

.header__top-button-cart::before {
  width: 15px;
  height: 15px;
  background-image: url("../images/icons/cart.svg");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
}

.header__button-buy {
  padding: 12px 18px;
  min-height: 42px;
}

.header__bottom {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}

.header__nav {
  grid-column: 1/-1;
}

.header-extra-link {
  color: var(--dark-background);
  font-size: 16px;
}

.header-extra-link:hover,
.header-extra-link:focus {
  color: var(--emphasis);
}

.header-extra-link:active {
  color: var(--light-type);
}

.header__description {
  color: var(--emphasis);
  font-style: italic;
  margin: 0 0 0 30px;
  font-size: 16px;
  min-height: 95px;
}

.header__contacts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: end;
}

.header__phone {
  display: flex;
  align-items: center;
  border: solid 3px var(--clear-background);
  margin-top: 3px;
  margin-bottom: 6px;
  padding: 7px 43px 8px 36px;
  color: var(--emphasis);
  font-weight: bold;
  font-size: 21px;
}

.header__phone::before {
  content: "";
  background-image: url("../images/icons/phone.svg");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  height: 19px;
  width: 19px;
  display: block;
  margin-right: 16px;
}

.header__address {
  line-height: 12px;
  font-size: 14px;
  padding: 3px 9px;
}

.header__sign {
  display: flex;
  justify-content: space-between;
  align-content: flex-start;
  flex-wrap: wrap;
  padding-top: 3px;
}

.signed {
  justify-content: center;
}

.user-box {
  display: flex;
  flex-wrap: nowrap;
  flex-grow: 1;
  background-color: var(--clear-background);
  margin-bottom: 4px;
}

.header__sign-button--authorization {
  margin-left: 19px;
  padding-right: 28px;
}

.header__sign-button--authorization::before {
  content: "";
  width: 20px;
  height: 17px;
  background-image: url("../images/icons/login.svg");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  filter: grayscale(100%);
  opacity: 0.335;
  margin-right: 7px;
  margin-left: -5px;
}

.header__sign-button--authorization:hover::before,
.header__sign-button--authorization:focus::before {
  filter: none;
  opacity: 1;
}

.header__sign-button--authorization:active::before {
  filter: grayscale(100%);
  opacity: 0.335;
}

.header__sign-button {
  display: flex;
  align-items: center;
  padding: 10px 24px 11px 23px;
  font-size: 21px;
  color: var(--basic-type);
  background-color: var(--basic-type-contrast);
  border-style: none;
  margin: 0;
}

.header__sign-button:hover,
.header__sign-button:focus {
  color: var(--emphasis);
}

.header__sign-button:active {
  color: var(--light-type);
}

.header__top-button {
  color: var(--basic-type-contrast);
  padding: 13px 27px 11px 46px;
  min-height: 42px;
  position: relative;
}

.header__top-button::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 23px;
  background-repeat: no-repeat;
  background-position: 0 0;
  opacity: 0.3;
}

.header__top-button:hover::before,
.header__top-button:focus::before {
  opacity: 1;
}

.header__top-button:active {
  background-color: var(--dark-background-push);
  color: var(--transparent-type);
}

.header__top-button:active::before {
  opacity: 0.3;
}

.logout-button {
  display: block;
  min-height: 45px;
  width: 55px;
  background-image: url("../images/icons/logout.svg");
  background-position: center;
  background-repeat: no-repeat;
  filter: grayscale(100%);
  opacity: 0.335;
}

.logout-button:hover,
.logout-button:focus {
  filter: none;
  opacity: 1;
}

.logout-button:active {
  filter: grayscale(100%);
  opacity: 0.335;
}

.sign-zone-link {
  position: relative;
  padding-left: 32px;
}

.sign-zone-link::before {
  content: "";
  width: 4px;
  height: 4px;
  background-color: var(--dark-background);
  position: absolute;
  border-radius: 50%;
  left: 15px;
  top: calc(50% - 2px);
}

.header-username {
  font-size: 21px;
  color: var(--basic-type);
  background-color: var(--basic-type-contrast);
  border-style: none;
  margin: 0;
}

.header-username {
  padding: 10px 24px 11px 68px;
  text-align: center;
  flex-grow: 1;
  position: relative;
}

.header-username::before {
  content: "";
  background-image: url("../images/icons/user.svg");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 20px;
  height: 18px;
  display: block;
  position: absolute;
  left: 16px;
  top: calc(50% - 10px);
  filter: grayscale(100%);
  opacity: 0.335;
}

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0 19px 0 23px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: var(--dark-background);
  box-shadow: 0 5px 0 var(--dark-background-extra);
}

.nav__item {
  flex-grow: 1;
}

.nav__item-link {
  display: block;
  text-align: center;
  color: var(--basic-type-contrast);
  background-color: var(--dark-background);
  padding: 17px 20px 14px;
  text-decoration: none;
}

.nav__item-link:hover,
.nav__item-link:focus {
  background-color: var(--dark-background-extra);
  position: relative;
  z-index: 1;
}

.nav__item-link:active {
  background-color: var(--dark-background-extra-push);
  color: var(--transparent-type);
}

.main__bottom {
  display: flex;
  justify-content: space-between;
  padding-top: 74px;
  padding-bottom: 70px;
}

.footer {
  color: var(--light-background);
  line-height: 18px;
}

.footer__nav {
  background-color: var(--dark-background);
  padding: 0 20px;
}

.footer__nav-inner {
  display: grid;
  grid-template-rows: 80px 1fr;
  grid-template-columns: 340px 1fr;
  padding-bottom: 56px;
}

.footer__nav-list {
  grid-row: 1/-1;
  grid-column: 2/-1;
  display: flex;
  flex-wrap: wrap;
  justify-content: right;
  margin: 21px -30px 0 24px;
  padding: 0;
  list-style: none;
}

.footer__nav-item {
  flex-grow: 1;
}

.footer__nav-link {
  padding: 20px;
  display: block;
  color: inherit;
  text-align: center;
}

.footer__nav-link:active {
  opacity: 0.5;
  text-decoration: none;
}

.footer__nav-link:hover,
.footer__nav-link:focus {
  text-decoration: underline;
}

.footer__nav-link--second {
  margin-left: 22px;
  color: var(--footer-type);
}

.footer__contacts {
  margin-bottom: -5px;
  margin-top: 9px;
}

.footer__contacts-link {
  padding: 4px;
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 0;
  color: inherit;
}

.footer__contacts-link:hover,
.footer__contacts-link:focus {
  text-decoration: underline;
}

.footer__link-bar {
  background-color: var(--dark-background-extra);
  padding: 35px 0 31px;
  font-size: 16px;
}

.footer__link-bar-inner {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.footer__copyright {
  width: 240px;
  margin: 0;
}

.footer__info {
  margin: 0 14px;
  display: flex;
  gap: 20px;
}

.footer__feedback {
  display: flex;
  flex-direction: column;
}

.footer__developer {
  display: flex;
  flex-direction: column;
}

.footer__info-link {
  color: var(--footer-type);
}

.footer__info-link:active {
  color: var(--emphasis);
  text-decoration: none;
}

.container {
  width: 960px;
  margin: 0 auto;
  padding: 0 10px;
}

.button-buy {
  box-shadow: inset 0 -3px 0 var(--deal-shadow);
  color: var(--basic-type-contrast);
  text-transform: uppercase;
  background-color: var(--deal);
  border-style: none;
  cursor: pointer;
}

.button-buy:hover,
.button-buy:focus {
  background-color: var(--deal-hover);
}

.button-buy:active {
  background-color: var(--deal);
  color: var(--transparent-type);
}

.about {
  width: 480px;
}

.about__title {
  margin-bottom: 0;
}

.about__text {
  margin-bottom: 0;
  margin-top: 26px;
  font-size: 13px;
}

.about__delivery-list {
  font-size: 18px;
  list-style: none;
  margin: -3px 0 0;
  padding: 0;
  line-height: 36px;
}

.about__delivery-item {
  padding-left: 36px;
  position: relative;
}

.about__delivery-item::before {
  content: "";
  position: absolute;
  background-color: var(--emphasis);
  width: 25px;
  height: 2px;
  left: 0;
  top: 17px;
}

.about__button {
  margin-top: 13px;
  padding-left: 40px;
  padding-right: 40px;
}

.brands {
  padding-bottom: 70px;
}

.brands__header {
  background-color: var(--heading-background);
  display: flex;
  justify-content: space-between;
  padding-left: 28px;
  padding-right: 14px;
  align-items: center;
  margin-bottom: 20px;
}

.brands__title {
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: normal;
  color: var(--dark-background);
  margin-top: 30px;
  margin-bottom: 29px;
  padding-right: 19px;
}

.brands__list {
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  margin: 0;
}

.brands__item-link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 145px;
  border: solid 1px var(--light-border);
}

.brands__item-link:hover,
.brands__item-link:focus {
  box-shadow: 0 4px 20px var(--transparent-shadow);
}

.brands__item-link:active {
  opacity: 0.5;
}

.contacts {
  width: 300px;
}

.contacts__title {
  margin-bottom: 0;
}

.contacts__text {
  margin-top: 26px;
  margin-bottom: 33px;
  font-size: 13px;
}

.contacts__button {
  margin-top: 18px;
  width: 100%;
}

.goods {
  padding-bottom: 70px;
}

.goods__header {
  background-color: var(--heading-background);
  display: flex;
  justify-content: space-between;
  padding-left: 28px;
  padding-right: 14px;
  align-items: center;
  margin-bottom: 20px;
}

.goods__title {
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: normal;
  color: var(--dark-background);
  margin-top: 30px;
  margin-bottom: 29px;
}

.goods__list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 18px 20px;
  margin-bottom: 48px;
  font-family: "PT Sans", "Arial", sans-serif;
}

.goods__list--full {
  grid-template-columns: repeat(4, 1fr);
  gap: 18px 20px;
  margin: 0;
}

.goods__item {
  font-weight: bold;
  width: 220px;
  min-height: 318px;
  border: 1px solid var(--light-border);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.goods__item:hover .goods__area-buttons,
.goods__item:focus .goods__area-buttons,
.goods__item:focus-within .goods__area-buttons {
  display: block;
}

.goods__item:hover,
.goods__item:focus,
.goods__item:focus-within {
  box-shadow: 0 4px 20px var(--transparent-shadow);
}

.goods__item--new {
  position: relative;
}

.goods__item--new a,
.goods__item--new button {
  position: relative;
  z-index: 1;
}

.goods__item--new::after {
  content: "";
  position: absolute;
  background-image: url("../images/decorative/new-flag-caps.svg");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 70px;
  height: 70px;
  right: 0;
  top: 0;
}

.goods__image {
  width: 184px;
  height: 166px;
  -o-object-fit: none;
  object-fit: none;
}

.goods__name {
  font-size: 19px;
  margin: 11px 30px 4px;
  text-align: center;
  letter-spacing: -0.036rem;
  line-height: 21px;
  flex-grow: 1;
}

.goods__past-price {
  font-size: 17px;
  margin: 0;
  color: var(--light-type);
  text-decoration: line-through;
}

.goods__actual-price {
  background-color: var(--emphasis);
  color: var(--basic-type-contrast);
  position: relative;
  left: -4px;
  height: 38px;
  width: 122px;
  margin: 10px 0 20px;
  padding-left: 11px;
  border-radius: 3px 0 0 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.goods__actual-price::after {
  content: "";
  position: absolute;
  height: 0;
  width: 0;
  border: 19px solid transparent;
  border-left: 19px solid var(--emphasis);
  right: -38px;
}

.goods__area-buttons {
  display: none;
  background-color: var(--clear-background);
  position: absolute;
  margin-left: -1px;
  padding: 37px 40px 40px 39px;
}

.goods__area-buttons button {
  position: relative;
  display: block;
  width: 135px;
  border-radius: 2px;
  font-family: "Cuprum", "Arial", sans-serif;
  font-size: 14px;
  cursor: pointer;
}

.goods__button-buy {
  margin-bottom: 10px;
  padding: 12px;
}

.goods__button-buy::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 30px;
  background-repeat: no-repeat;
  background-position: 0 0;
  opacity: 0.3;
}

.goods__button-bookmark {
  color: var(--dark-background);
  background-color: transparent;
  text-transform: uppercase;
  border-color: var(--deal);
  border-style: solid;
  border-width: 3px;
  padding-top: 9px;
  padding-bottom: 8px;
}

.goods__button-bookmark:hover,
.goods__button-bookmark:focus {
  border-color: var(--dark-background);
}

.goods__button-bookmark:active {
  opacity: 0.5;
}

.services {
  padding-top: 68px;
}

.services__text {
  width: 400px;
  margin-bottom: 71px;
}

.services p,
.catalog-article p {
  font-size: 13px;
  margin-bottom: 0;
}

.services,
.catalog-article {
  background-color: var(--light-background);
}

.tabs {
  padding-top: 71px;
  padding-bottom: 94px;
  position: relative;
  display: grid;
  grid-template-columns: 240px 1fr;
  grid-gap: 0 80px;
}

.tabs::after {
  content: "";
  position: absolute;
  background-image: url("../images/decorative/services-background.png");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 10px;
  height: 279px;
  top: 21px;
  left: 230px;
}

.tabs__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.tabs-information {
  display: none;
  width: 315px;
  line-height: 25px;
  grid-row-start: 1;
  grid-column-start: 2;
  grid-row-end: 5;
}

.tabs-information h3 {
  text-transform: uppercase;
  font-size: 30px;
  color: var(--dark-background);
  letter-spacing: -1px;
  margin: 0 0 31px;
  position: relative;
  z-index: 1;
}

.tabs-information p {
  position: relative;
  z-index: 1;
}

.tabs-information::after {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  top: 89px;
  right: 1px;
  z-index: 0;
}

.box-delivery::after {
  background-image: url("../images/icons/delivery.png");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 468px;
  height: 261px;
}

.box-warranty h3 {
  margin-bottom: 26px;
}

.box-warranty::after {
  background-image: url("../images/icons/guarantee.png");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 389px;
  height: 283px;
  top: 66px;
  right: -8px;
}

.box-credit h3 {
  margin-bottom: 30px;
}

.box-credit p {
  margin-bottom: 27px;
}

.box-credit .button {
  padding: 6px 43px 7px 46px;
  border-radius: 3px;
}

.box-credit::after {
  background-image: url("../images/icons/credit.png");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 465px;
  height: 285px;
  top: 67px;
  right: -10px;
