}

.sort-button:hover,
.sort-button:focus {
  color: var(--basic-type);
  text-decoration-style: solid;
}

.sort-category-current {
  color: var(--emphasis);
  opacity: 1;
  text-decoration: none;
}

.sort-arrow-box {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.sort-category-box {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.sort-category-box li {
  display: flex;
}

.sort-arrow {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  width: 32px;
  padding: 10px;
}

.sort-arrow::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-color: var(--emphasis);
}

.sort-arrow:hover::before,
.sort-arrow:focus::before {
  filter: brightness(0%);
  opacity: 1;
}

.arrow-reverse::before {
  border: 5px solid transparent;
  border-bottom: 11px solid var(--emphasis);
  top: calc(50% - 10px);
  left: calc(50% - 5px);
  filter: grayscale(100%);
  opacity: 0.335;
}

.arrow-regular::before {
  border: 5px solid transparent;
  border-top: 11px solid var(--emphasis);
  filter: grayscale(100%);
  opacity: 0.335;
  top: calc(50% - 4px);
  left: calc(50% - 5px);
}

.sort-arrow-current::before {
  filter: none;
  opacity: 1;
}

.filter-form {
  display: flex;
  flex-direction: column;
}

.range-box {
  background-color: var(--catalog-background);
}

.range span {
  background-color: var(--catalog-background);
}

.price-controls span {
  display: inline-block;
  vertical-align: middle;
  margin-top: 7px;
}

.pages-list {
  display: flex;
  list-style: none;
  padding: 0;
}

.catalog-article {
  padding-top: 68px;
  padding-bottom: 68px;
}

